import React, {useState} from 'react';

function RealEstateChatbot(props) {
    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([]);

    const handleSubmit = async (event) => {
        event.preventDefault();
        setMessages(messages => [...messages, {text: input, sender: 'user'}]);

        // Make a POST request to the server
        try {
            const result = await fetch('http://ec2-18-116-61-157.us-east-2.compute.amazonaws.com/generate-text?prompt=' + input, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                },
            });

            const resultData = await result.json();
            // Set the result
            setMessages(messages => [...messages, {text: resultData.text, sender: 'bot'}]);
            setInput('');
        } catch (e) {
            console.log(e)
        }

    };

    const chatStyles = {
        display: 'flex',
        flexDirection: 'column',
        padding: '10px',
        maxHeight: '500px',
        overflowY: 'scroll',
        border: '1px solid #ddd',
        borderRadius: '5px',
        width: '300px',
        margin: '0 auto',
    };

    const userMessageStyles = {
        alignSelf: 'flex-end',
        backgroundColor: '#0084FF',
        color: 'white',
        borderRadius: '20px',
        padding: '10px',
        margin: '5px',
    };

    const botMessageStyles = {
        alignSelf: 'flex-start',
        backgroundColor: '#f0f0f0',
        color: 'black',
        borderRadius: '20px',
        padding: '10px',
        margin: '5px',
    };

    return (
        <div>
            <form onSubmit={handleSubmit}>
                <input type="text" value={input} onChange={e => setInput(e.target.value)}
                       placeholder="Type a message..."/>
                <input type="submit" value="Send"/>
            </form>
            <div style={chatStyles}>
                {messages.map((message, index) => (
                    <div key={index} style={message.sender === 'user' ? userMessageStyles : botMessageStyles}>
                        {message.text}
                    </div>
                ))}
            </div>
        </div>
    );
}

export default RealEstateChatbot;